class RouterComponent{
	constructor() {}
	Navigation(r){
		return require(['../views/Navigation'],r)
	}
	My(r){
		return require(['../views/My'],r)
	}
	Search(r){
		return require(['../views/Search'],r)
	}
	Videos(r){
		return require(['../views/Videos'],r)
	}
	Song(r){
		return require(['../components/Song'],r)
	}
	Player(r){
		return require(['../components/Player'],r)
	}
}
export default new RouterComponent();