import routerComponent from './routerComponent.js';

export default [
	{
		path:'/',
		name:'navigation',
		component:routerComponent.Navigation,
		
		//二级路由
		children:[
			{
				path:'my',
				name:'my',
				component:routerComponent.My
			},
			{
				path:'search',
				name:'search',
				component:routerComponent.Search,
			},
			{
				path:'videos',
				name:'videos',
				component:routerComponent.Videos,
			},
// 				{
// 				    path: '*',
// 				    redirect: { name: 'song' }
// 				}
		]
	},
	{
		path:'song',
		name:'song',
		component:routerComponent.Song
	},
	{
		path:'player',
		name:'player',
		component:routerComponent.Player
	},
]